<template>
  <div id="app">
    <navigation :navVal="activeNav" />
    <router-view class="content" @changeNav="change" />
  </div>
</template>
<script>
import navigation from "@/components/Navigation.vue";
export default {
  data() {
    return {
      activeNav: {
        title: "电影",
        color: "movie",
      }
    };
  },
  components: {
    navigation
  },
  methods: {
    change(obj) {
      this.activeNav.title = obj.title;
      this.activeNav.color = obj.color;
    }
  }
};
</script>
