<template>
  <div :class="colorName">
    <header>
      <span @click="routerPush(footerList[0])">首页</span>
      <h2>{{titleName}}</h2>
    </header>
    <footer>
      <ul>
        <li
          :class="{active:colorName == item.color}"
          @click="routerPush(item)"
          v-for="(item, index) in footerList"
          :key="index"
        >{{item.name}}</li>
      </ul>
    </footer>
  </div>
</template>
<script>
export default {
  data() {
    return {
      titleName: "电影",
      colorName: "movie",
      footerList: [
        {
          name: "电影",
          routerPath: "/",
          color: "movie"
        },
        {
          name: "音乐",
          routerPath: "/music",
          color: "music"
        },
        {
          name: "游戏",
          routerPath: "/game",
          color: "game"
        },
        {
          name: "聊天",
          routerPath: "/talk",
          color: "talk"
        }
      ]
    };
  },
  props: ["navVal"],

  methods: {
    routerPush(item) {
        this.colorName = item.color;
        this.titleName = item.name;
        this.$router.push(item.routerPath);
    }
  },
  computed: {
    propsVal() {
      // this.activeClass = this.navVal.className;
      // this.activeTitle = this.navVal.title
      return this.navVal.title;
    }
  },
  watch: {
    propsVal() {
      this.titleName = this.navVal.title;
      this.colorName = this.navVal.color;
    }
  }
};
</script>
<style scoped>
header,
footer {
  position: fixed;
  width: 100%;
  height: 1rem;
  background-color: rgb(33, 150, 243);
  color: #fff;
  text-align: center;
}
header {
  top: 0;
}
header span {
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
}
header h1{
  position: absolute;
    top: 50%;
    left: 50%;
  transform: translate(-50%,-50%);
}
footer {
  bottom: 0;
}
footer ul {
  display: flex;
  justify-content: center;
  width: 100%;
}
footer ul li {
  flex: 1;
  text-align: center;
  height: 1rem;
  line-height: 1rem;
  font-size: 0.3rem;
  color: aliceblue;
}
.movie header,
.movie footer {
  background: rgb(235, 94, 188);
}
.music header,
.music footer {
  background: rgb(47, 143, 17);
}
.game header,
.game footer {
  background: rgb(54, 63, 52);
}
.talk header,
.talk footer {
  background: rgb(51, 17, 143);
}
footer ul li.active {
  font-weight: bolder;
  color: #000;
}
</style>