<template>
  <div>
    <div>
      <div class="loading" v-show="isLoading">
        <img src="../../assets/img/timg.gif" alt />
      </div>
    </div>
    <transition name="fade" mode="in-out">
      <div id="container" v-if="isShow">
        <div class="clearfix" id="head">
          <img id="movie-img" :src="'https://images.weserv.nl/?url='+detailList.images.small" />
          <h3>{{detailList.title}}</h3>
          <div class="list-content">
            {{detailList.countries[0]}}
            <br />
            时长:{{detailList.durations[0]}}
            <br />
            时间：{{detailList.year}}
            <br />影片类型:
            <span v-for="(item, index) in detailList.genres" :key="index">{{item}}|</span>
          </div>
        </div>

        <p id="description">
          <strong>剧情简介：</strong>
          {{detailList.summary}}
        </p>
      </div>
    </transition>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      detailList: {},
      isShow: false,
      isLoading: false
    };
  },
  created() {
    this.getData();
  },

  methods: {
    getData() {
      (this.isLoading = true),
        axios
          .get(
            `https://bird.ioliu.cn/v2?url=https://douban.uieee.com/v2/movie/subject/${this.$route.params.id}`
          )
          .then(res => {
            this.isShow = true;
            this.detailList = res.data
            this.isLoading = false;
          });
    }
  }
};
</script>
<style scoped>
.loading {
  position: fixed;
  top: 1rem;
  left: 0;
  right: 0;
  bottom: 1rem;
  background-color: #fff;
  opacity: 0.5;
}
.loading img {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  transition: all 0.5s ease;
}
.fade-enter-to {
  opacity: 1;
}
#container {
  margin-top: 1.2rem;
  height: 5.3rem;
  background: rgb(51, 90, 127);
  width: 100%;
  position: relative;
}
#head {
  height: 3.5rem;
}
.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
#movie-img {
  display: block;
  width: 2rem;
  position: absolute;
  top: 0.3rem;
  left: 5%;
}
h3 {
  position: absolute;
  top: 5%;
  left: 2.5rem;
  color: #ffffff;
}
#description {
  padding-top: 0.2rem;
  padding-left: 0.3rem;
  padding-right: 0.3rem;
  position: absolute;
  color: #ffffff;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3; /*此为两行，设置行数*/
  -webkit-box-orient: vertical; /*子元素垂直排列*/
}
.list-content {
  color: #fff;
  position: absolute;
  left: 2.5rem;
  top: 1rem;
}
</style>